import whistle from "../../data/whistle.wav"

let muted = (localStorage.getItem("muted") && localStorage.getItem("muted") === "true")
    ? true
    : false

const setMuted = (newMuted) => {
    muted = newMuted
    localStorage.setItem("muted", newMuted)
}

const getMuted = () => {
    return muted
}

const playBeep = () => {
    if (muted) return
    var snd = new Audio("data:audio/wav;base64,");
    snd.volume = 0.1
    snd.play()
}

const playWhistle = () => {
    // credit to: https://freesound.org/people/16GStepankovaS/sounds/499386/
    if (muted) return
    var snd = new Audio(whistle)
    snd.volume = 0.1
    snd.play()
}
export default { playBeep, playWhistle, setMuted, getMuted }